"use client";

import Link from "next/link";
import { usePathname } from "next/navigation";
import { useState, useEffect } from "react";

export default function Sidebar() {
  const pathname = usePathname();
  const [openMenu, setOpenMenu] = useState(null);

  const menus = [
    {
      name: "Overview",
      href: "/",
    },
    {
      name: "Getting Started",
      href: "/gettingstarted",
    },
    {
      name: "Employee Management",
      children: [
        { name: "Employee List", href: "/employeemanage/data" },
        { name: "Create New Employee", href: "/employeemanage/new-employee" },
        { name: "Joining", href: "/employeemanage/joining" },
        { name: "Address & Contact", href: "/employeemanage/address" },
        { name: "Attendance", href: "/employeemanage/attendance" },
        { name: "Salary", href: "/employeemanage/salary" },
        { name: "Personal", href: "/employeemanage/personal" },
        { name: "Profile", href: "/employeemanage/profile" },
        { name: "Exit", href: "/employeemanage/exit" },
      ],
    },
    {
      name: "Claims & Travel",
      children: [
        { name: "Employee Expense", href: "/claimtravel/expense" },
        { name: "Create Expense", href: "/claimtravel/addexpense" },
        { name: "Employee Advance", href: "/claimtravel/advance" },
        { name: "Add New Advance", href: "/claimtravel/addadvance" },
        { name: "Expense Claim Type", href: "/claimtravel/expense-type" },
        { name: "Create Expense Claim Type", href: "/claimtravel/add-expense-type" },
        { name: "Employee Travel", href: "/claimtravel/travel" },
        { name: "Create Travel", href: "/claimtravel/add-travel" },
      ],
    },
    {
      name: "Shift & Attendance",
      children: [
        { name: "Shift Attendance Management", href: "/shift-attendance/shift-attendance-management" },
        { name: "Attendance Dashboard", href: "/shift-attendance/attendance-dashboard" },
        { name: "Add Attendance", href: "/shift-attendance/add-attendance" },
        { name: "Attendance Activity", href: "/shift-attendance/attendance-activity" },
        { name: "Attendance Report", href: "/shift-attendance/attendance-report" },
        { name: "Activity Report", href: "/shift-attendance/activity-report" },
        { name: "Shift Report", href: "/shift-attendance/shift-report" },
        { name: "Attendance Summary", href: "/shift-attendance/attendance-summary" },
        { name: "Data Checkpoint", href: "/shift-attendance/data-checkpoint" },
        { name: "Add Checkpoint", href: "/shift-attendance/add-checkpoint" },
        { name: "Checkpoint Record", href: "/shift-attendance/checkpoint-record" },
        { name: "Add Checkpoint Record", href: "/shift-attendance/add-record" },
        { name: "Checkpoint Assignment", href: "/shift-attendance/checkpoint-assignment" },
        { name: "Shift Type", href: "/shift-attendance/shift-type" },
        { name: "Add Shift Type", href: "/shift-attendance/add-shift-type" },
        { name: "Shift Assignment", href: "/shift-attendance/shift-assignment" },
        { name: "Add Shift Assignment", href: "/shift-attendance/add-shift-assignment" },
        { name: "Add Shift Assignment Bulk", href: "/shift-attendance/bulk-assignment" },
        { name: "Shift Assignment Schedule", href: "/shift-attendance/assignment-schedule" },
      ],
    },
    {
      name: "Leaves",
      children: [
        { name: "Employee Leave", href: "/leaves/employee-leaves" },
        { name: "Holiday List", href: "/leaves/holiday-list" },
        { name: "Add New Holiday", href: "/leaves/add-new-holiday" },
        { name: "Leave Type", href: "/leaves/leave-type" },
        { name: "Add New Leave Type", href: "/leaves/add-new-leave" },
        { name: "Leave Application", href: "/leaves/leave-application" },
        { name: "Add Leave Application", href: "/leaves/add-leave-application" },
        { name: "Leave Allocation", href: "/leaves/leave-allocation" },
        { name: "Add Leave Allocation", href: "/leaves/add-leave-allocation" },
      ],
    },
    {
      name: "Company",
      children: [
        { name: "Publisher", href: "/company/publisher" },
        { name: "Categories", href: "/company/categories" },
        { name: "Add New Categories", href: "/company/add-new-categories" },
        { name: "Blog & News", href: "/company/blog-&-news" },
        { name: "Events Calendar", href: "/company/events-calendar" },
        { name: "Add New Events Calendar", href: "/company/add-new-events-calendar" },
        { name: "Broadcast Notification", href: "/company/broadcast-notification" },
        { name: "Add New Broadcast Notification", href: "/company/add-new-broadcast-notification" },
        { name: "Companies", href: "/company/companies" },
        { name: "HR Setting", href: "/company/hr-setting" },
        { name: "HRMS Setting", href: "/company/hrms-setting" },
        { name: "HRMS Setting Page Guidance", href: "/company/hrms-page-guidance" },
        { name: "My Account Page", href: "/company/my-account" },
        { name: "My Account Page (Detail Guidance)", href: "/company/my-account-page" },
        { name: "Email Settings Page", href: "/company/email-settings" },
        { name: "Security Settings", href: "/company/security-settings" },
        { name: "Preferences Settings", href: "/company/preferences-settings" },
        { name: "Deactivation Account", href: "/company/deactivation-account" },
        { name: "User Access Controll", href: "/company/user-access-controll" },
        { name: "Role List", href: "/company/role-list" },
        { name: "Add New Role", href: "/company/new-role" },
        { name: "Designation List", href: "/company/designation-list-page" },
        { name: "Add New Designation", href: "/company/add-designation" },
        { name: "Data Department", href: "/company/department" },
        { name: "Grade", href: "/company/grade" },
        { name: "Branch", href: "/company/branch" },
      ],
    },
    {
      name: "Payroll",
      children: [
        { name: "Salary Component", href: "/payroll/salary-component" },
        { name: "Salary Structure", href: "/payroll/salary-structure" },
        { name: "Salary Structure Assignment", href: "/payroll/salary-structure-assignment" },
        { name: "Bulk Salary Structure Assignment", href: "/payroll/bulk-salary-structure-assignment" },
        { name: "Payroll Periode", href: "/payroll/payroll-periode" },
        { name: "Payroll Entry", href: "/payroll/payroll-entry" },
        { name: "Salary Slip", href: "/payroll/salary-slip" },
        { name: "Cost Component", href: "/payroll/cost-component" },
      ],
    },
  ];

  // Buka otomatis menu jika salah satu child-nya aktif
  useEffect(() => {
    menus.forEach((menu, idx) => {
      if (menu.children?.some((child) => child.href === pathname)) {
        setOpenMenu(idx);
      }
    });
  }, [pathname]);

  return (
    <div className="w-64 bg-gray-50 shadow flex flex-col sticky top-0 h-screen overflow-y-auto">
      {/* Header Sidebar */}
      <div className="p-5 border-b border-gray-200 flex flex-col items-center justify-center text-center space-y-0">
        <img
          src="/duluin-hrms.png"
          alt="Logo HRMS"
          className="w-28 h-auto object-contain -my-2 block"
        />
        <h3 className="text-base font-bold text-gray-800 leading-tight mt-5">
          Documentation
        </h3>
      </div>

      {/* Menu Items */}
      <nav className="flex flex-col p-4 space-y-2">
        {menus.map((menu, idx) => (
          <div key={menu.name}>
            {/* Case: Menu langsung tanpa children */}
            {menu.href ? (
              <Link
                href={menu.href}
                className={`block px-4 py-2 rounded-lg transition ${
                  pathname === menu.href
                    ? "bg-blue-100 text-blue-600 font-semibold"
                    : "text-gray-600 hover:bg-blue-50"
                }`}
              >
                {menu.name}
              </Link>
            ) : (
              <>
                {/* Case: Menu dengan children */}
                <button
                  onClick={() => setOpenMenu(openMenu === idx ? null : idx)}
                  className={`w-full text-left px-4 py-2 rounded-lg transition ${
                    openMenu === idx
                      ? "bg-blue-100 text-blue-600 font-semibold"
                      : "text-gray-600 hover:bg-blue-50"
                  }`}
                >
                  {menu.name}
                </button>

                {/* Dropdown */}
                {menu.children && openMenu === idx && (
                  <div className="ml-6 mt-1 flex flex-col space-y-1">
                    {menu.children.map((child) => (
                      <Link
                        key={child.href}
                        href={child.href}
                        className={`block px-3 py-1 rounded-md text-sm transition ${
                          pathname === child.href
                            ? "bg-blue-100 text-blue-600 font-medium"
                            : "text-gray-500 hover:bg-blue-50"
                        }`}
                      >
                        {child.name}
                      </Link>
                    ))}
                  </div>
                )}
              </>
            )}
          </div>
        ))}
      </nav>
    </div>
  );
}
